import React from 'react'
import UserForm from './UserForm'
import {appName} from '../constants'
import '../style.css'

export class RegisterPage extends React.Component {

    constructor(){
        super()

        this.state = {
            registered : false,
            referenceId : ''
        }
    }

    componentDidMount(){
        document.title = appName + " | Register Incident"
    }

    updateRegistrationStatus = (response) => {
        console.log(response)
        this.setState({
           registered : response.status,
           referenceId : response.referenceId
        })
    }


    render() {
        return (
            <React.Fragment>
                <div className="HSMainContainer">
                    <div className="useViewTopBar">
                        <div className="logoImage">{appName}</div> 
                    </div>
                    <div>
                        <div className="RSLeft">
                            <div className="RSbigBigFont">{appName}</div>
                            <div className="dash"></div>
                            <p>
                                <br/>
                                Lorem ipsum dolor sit amet, consectetur adipiscing elit. Nullam eget feugiat est, quis gravida mi. Nullam sagittis ante turpis, id rhoncus nibh fringilla eget. Curabitur erat sapien, tempor quis nulla in, aliquet blandit libero. Nam lacinia nisl sollicitudin tempor suscipit. Nullam sed arcu at tortor condimentum elementum eget et velit.
                            </p>
                            <p>
                                Curabitur erat sapien, tempor quis nulla in, aliquet blandit libero. Nam lacinia nisl sollicitudin tempor suscipit. Nullam sed arcu at tortor condimentum elementum eget et velit.
                            </p>
                        </div>
                        <div className="RSRight">
                            <div className={!this.state.registered ? "HSRightRegistrationFormHolder" : "regSuccessMessage"}>
                                {
                                    this.state.registered ? 
                                    <React.Fragment>
                                        <div className="pad20">
                                            <b>Registration Successful</b><br/>
                                            Your can note down the reference ID <b>{this.state.referenceId}</b>.
                                        </div>
                                    </React.Fragment>
                                    :
                                    <React.Fragment>
                                        <div className="mdl-card__supporting-text">
                                            Fill in the details to register a new incident.
                                            <UserForm updateRegistrationStatus={this.updateRegistrationStatus}/>
                                        </div>
                                    </React.Fragment>
                                }
                            </div>
                        </div>
                    </div>
                </div>
            </React.Fragment>
        )
    }
}

export default RegisterPage



