import React from 'react'
import {appName} from './constants'
import './style.css'

function HomePage(props) {
    const changeChildArrow = ()=>{}
    return (
        <React.Fragment>
            <div className="HSMainContainer">
                <div className="useViewTopBar">
                    <div className="logoImage">{appName}</div> 
                </div>
                <div>
                    <div className="HSLeft">
                        Welcome to
                        <div className="bigBigFont">{appName}</div>
                        <div className="dash"></div>
                        <div className="bigFont">Mock Case Registration Portal</div>
                    </div>
                    <div className="HSRight">
                    Lorem ipsum dolor sit amet, consectetur adipiscing elit. Nullam eget feugiat est, quis gravida mi. Nullam sagittis ante turpis, id rhoncus nibh fringilla eget. Curabitur erat sapien, tempor quis nulla in, aliquet blandit libero. Nam lacinia nisl sollicitudin tempor suscipit. Nullam sed arcu at tortor condimentum elementum eget et velit.
                    <br/><br/>
                    <button className="HSNavButton" 
                        onClick={()=>props.history.push('register')} 
                        onMouseEnter={()=>changeChildArrow} onMouseLeave={()=>changeChildArrow}
                    >
                        REGISTER&emsp;
                        <i class="fa fa-caret-right" aria-hidden="true"></i>
                    </button>
                    </div>
                </div>
            </div>
        </React.Fragment>
    )
}

export default HomePage



